import React from 'react';
import { IonButton, IonHeader, IonNavLink, IonToolbar, IonTitle, IonButtons, IonIcon, IonBackButton } from '@ionic/react';
import {helpCircle,library,search, home} from 'ionicons/icons';

import AboutLink from '../routes/locations/About'
import DashboardLink from '../routes/locations/Dashboard'
import LibraryLink from '../routes/locations/Library'
import SearchLink from '../routes/locations/Search'

const HeadBand = (prop) => {
    const activeMenu = (chkNamae) => {
        return (chkNamae === prop.namae ) ? true : false;
    }

    return (
    <IonHeader translucent={true} className='bandana'>

        <IonToolbar>
            <IonButtons slot="start">
                <IonBackButton></IonBackButton>
            </IonButtons>

            <IonTitle>{prop.namae}</IonTitle>

            <IonButtons slot="start">

                <IonButton disabled={activeMenu('SG Los Menéndez')}>
                    <IonNavLink routerDirection="root" component={() => <DashboardLink />}>
                        <IonIcon slot="icon-only" icon={home}></IonIcon>
                    </IonNavLink>
                </IonButton>

                <IonButton disabled={activeMenu('Archivos')}>
                    <IonNavLink routerDirection="forward" component={() => <LibraryLink />}>
                        <IonIcon slot="icon-only" icon={library}></IonIcon>
                    </IonNavLink>
                </IonButton>

                <IonButton disabled={activeMenu('Buscar')}>
                    <IonNavLink routerDirection="forward" component={() => <SearchLink />}>
                        <IonIcon slot="icon-only" icon={search}></IonIcon>
                    </IonNavLink>
                </IonButton>

                <IonButton disabled={activeMenu('Quiénes Somos')}>
                    <IonNavLink routerDirection="forward" component={() => <AboutLink />}>
                        <IonIcon slot="icon-only" icon={helpCircle}></IonIcon>
                    </IonNavLink>
                </IonButton>

            </IonButtons>

        </IonToolbar>

    </IonHeader>
    )
}
export default HeadBand