import React from 'react';
import { IonCard, IonCardContent, IonCardHeader, IonCardSubtitle, IonCardTitle, IonContent, IonIcon } from '@ionic/react';
import {informationCircleSharp, construct} from 'ionicons/icons';

import HeadBand from '../../gear/HeadBand';

function About() {
    return (
        <>
            <HeadBand namae='Quiénes Somos' />
            <IonContent class="ion-padding" color='light' fullscreen={true}>
                <div className="custom-ion-card-parent">
                    <IonCard className='custom-ion-card'>
                        <IonCardHeader>
                            <IonCardTitle style={{lineHeight: 1,display:'flex'}}>
                                <IonIcon icon={informationCircleSharp} ></IonIcon>
                                <span>Información</span>
                            </IonCardTitle>
                            <IonCardSubtitle>SG Los Menéndez</IonCardSubtitle>
                        </IonCardHeader>

                        <IonCardContent>
                            <div style={{textAlign:'center',fontSize:'5em'}}>
                                <IonIcon icon={construct} ></IonIcon>
                            </div>
                            Disculpe las molestias estamos en construcción.
                        </IonCardContent>
                    </IonCard>
                </div>
            </IonContent>
        </>
    );
}

export default About;